import { Component, OnInit, AfterViewInit, OnDestroy } from '@angular/core';
import { TraitementService } from '../../_services/traitement.service';
import {Router } from '@angular/router';


declare var $:any;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
})
export class LoginComponent implements OnInit, AfterViewInit, OnDestroy {
 
message:Number=0;
  constructor(private traitService:TraitementService,private router:Router) { }
  valider(user){
    this.traitService.valider(user)
    .subscribe(resp=>{
      let jwt=resp.headers.get('Authorization');
        this.traitService.saveToken(jwt);
      this.router.navigateByUrl("/index")
    },error=>{
    this.message=1;
    })
    
    
      }






//concernant la template
  ngOnInit() {
    $('body').addClass('empty-layout bg-silver-300');
  }

  ngAfterViewInit() {
    $('#login-form').validate({
        errorClass: "help-block",
        rules: {
            email: {
                required: true,
                email: true
            },
            password: {
                required: true
            }
        },
        highlight: function(e) {
            $(e).closest(".form-group").addClass("has-error")
        },
        unhighlight: function(e) {
            $(e).closest(".form-group").removeClass("has-error")
        },
    });
  }

  ngOnDestroy() {
    $('body').removeClass('empty-layout bg-silver-300');
  }

}



