import { Component, OnInit } from '@angular/core';
import { Article } from '../../classes/article';
import { ArticleService } from '../../_services/article.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-ajouter-modifier-article',
  templateUrl: './ajouter-modifier-article.component.html',
  styleUrls: ['./ajouter-modifier-article.component.css']
})
export class AjouterModifierArticleComponent implements OnInit {
  private article:Article;
  constructor(private router:Router,private articleService:ArticleService) {
    this.article=this.articleService.getterArticle();
   }

  ngOnInit() {
  }
  processForm(){
    if(this.article.code==undefined){
      this.articleService.AjouterArticle(this.article).subscribe((article)=>{
        alert('L\'ajout a été bien effectué');
        this.router.navigateByUrl("/articles")
        },(error)=>{
          alert('erreur d\'ajout  ');
        }
      );

    } else{ 
    this.articleService.ModifierArticle(this.article).subscribe((article)=>{
      //console.log(Client);
      alert('La modification a été bien effectuée');
      this.router.navigateByUrl("/articles");
      },(error)=>{
        alert('erreur de modification  ');
      }
    );
    
  }
    }
}



