import { Component, OnInit, AfterViewInit } from '@angular/core';
import { TraitementService } from '../../../_services/traitement.service';
import { Router } from '@angular/router';
import { error } from 'protractor';
import { User } from '../../../classes/user';
import { FormControl } from '@angular/forms';


declare var $:any;

@Component({
  selector: 'app-input-masks',
  templateUrl: './input-masks.component.html',
  styleUrls: ['./input-masks.component.css']
})
export class InputMasksComponent implements OnInit {
  private user:User;
  message:number=0;
  passwordConfirmer;

  constructor(private traitService:TraitementService,private router:Router) { }

  ngOnInit() {
    this.user=this.traitService.getUser();
  }


  FormControlFormControl
  processForm(){
    if(this.user.username==undefined && this.passwordConfirmer==this.user.password){
      this.traitService.AjouterUser(this.user).subscribe((user)=>{
          console.log(user);
          alert('L\opération a été bien effectuer  ');
          this.router.navigateByUrl("/tables/datatables")
        },(error)=>{
          alert('erreur d\'ajout  ');

        }
      );


    }else if(this.passwordConfirmer!=this.user.password){

      alert("les mots de passes ne sont pas identiques")

    }

    else{
      this.traitService.ModifierUser(this.user).subscribe((user)=>{
          console.log(user);
          alert('L\opération a été bien effectuer  ');
          this.router.navigateByUrl("/tables/datatables");
        },(error)=>{
          alert('erreur de modification  ');
          console.log(error);
        }
      );

    }

  }




}



