import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { User } from '../classes/user';


@Injectable()
export class TraitementService {
private user:User;
private host:string="http://localhost:8080";
private jwtoken=null;
  constructor(private http:HttpClient) { }

  valider(user){// pour l'authentification
    return this.http.post(this.host+"/login",user,{observe:'response'});
  }
  saveToken(jwt:string){
    this.jwtoken=jwt;
    localStorage.setItem('token',jwt);
}
loadtoken(){
  this.jwtoken=localStorage.getItem('token');
  }
getUsers(){
  if(this.jwtoken==null) this.loadtoken();
  return this.http.get(this.host+"/listUtilisateurs",{headers:new HttpHeaders({'Authorization':this.jwtoken})})

}
AjouterUser(user){
  if(this.jwtoken==null) this.loadtoken();
  return this.http.post(this.host+"/saveUser",user,{headers:new HttpHeaders({'Authorization':this.jwtoken})});
}
SupprimerUser(username){
  if(this.jwtoken==null) this.loadtoken();
  return this.http.delete(this.host+"/supprimerUser/"+username,{headers:new HttpHeaders({'Authorization':this.jwtoken})});
}
AfficherUser(user){
 this.user=user;
}
ModifierUser(user){
  if(this.jwtoken==null) this.loadtoken();
  return this.http.put(this.host+'/modifierUser',user,{headers:new HttpHeaders({'Authorization':this.jwtoken})});

}
RechercherUser(username){
  if(this.jwtoken==null) this.loadtoken();
  return this.http.get(this.host+"/findUserByUsername/"+username,{headers:new HttpHeaders({'Authorization':this.jwtoken})})
}
setUser(user:User){
  this.user=user;
}
getUser(){
  return this.user;
}
}



