import { AjouterModifierClientComponent } from './pages/ajouter-modifier-client/ajouter-modifier-client.component';
import { AjouterModifierArticleComponent } from './pages/ajouter-modifier-article/ajouter-modifier-article.component';
import { ListGrossistesComponent } from './pages/list-grossistes/list-grossistes.component';
