import { InventaireComponent } from './pages/inventaire/inventaire.component';
import { BonLivraisonComponent } from './pages/bon-livraison/bon-livraison.component';
import { BonReceptionComponent } from './pages/bon-reception/bon-reception.component';
import { AvoirComponent } from './pages/avoir/avoir.component';
import { ListVentesComponent } from './pages/list-ventes/list-ventes.component';
