import { AjouterModifierGrossisteComponent } from './pages/ajouter-modifier-grossiste/ajouter-modifier-grossiste.component';
import { CommandeService } from './_services/commande.service';
import { LigneCommandeService } from './_services/ligne-commande.service';


@NgModule({
  declarations: [
    AppComponent,
    UtilisateursComponent,
    AjouterModifierUtilisateurComponent,
    ListClientsComponent,
    ListArticlesComponent,
    AjouterModifierClientComponent,
    AjouterModifierArticleComponent,
    ListGrossistesComponent,
    AjouterModifierCommandeComponent,
    ListCommandComponent,
    InventaireComponent,
    BonLivraisonComponent,
    BonReceptionComponent,
    AvoirComponent,
    ListVentesComponent,
    AjouterModifierVenteComponent,
    AjouterModifierGrossisteComponent,
    
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    LayoutModule,
    NgxPaginationModule,
    
  ],
  providers: [ScriptLoaderService,TraitementService,ClientService,GrossisteServiceService, ArticleService,CommandeService,LigneCommandeService],
  bootstrap: [AppComponent]
})
export class AppModule { }



