import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Commande } from '../classes/commande';
import { LigneCommande } from '../classes/ligne-commande';

@Injectable()
export class CommandeService {
  private host:string="http://localhost:8080";
  private jwtoken=null;
  private commande:Commande;
  constructor(private http:HttpClient) { }

  saveToken(jwt:string){
    this.jwtoken=jwt;
    localStorage.setItem('token',jwt);
}
loadtoken(){
  this.jwtoken=localStorage.getItem('token');
  }
  getCommandes(){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.get(this.host+"/listArticles",{headers:new HttpHeaders({'Authorization':this.jwtoken})})
  }

 
  AjouterCommande(commande:Commande){
    let body = JSON.stringify({ commande });    
    if(this.jwtoken==null) this.loadtoken();
    return this.http.post(this.host+"/ajouterCommande",body,{headers:new HttpHeaders({'Authorization':this.jwtoken,'Content-Type':'application/json' })});
  
  }
  ModifierCommande(commande:Commande){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.put(this.host+"/modifierArticle",commande,{headers:new HttpHeaders({'Authorization':this.jwtoken})});
  }
  getterCommande(){
    return this.commande;
  }
  setterCommande(commande){
    this.commande=commande;
  }


}



