import { Component, OnInit } from '@angular/core';
import { Client } from '../../classes/client';
import { ClientService } from '../../_services/client.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-ajouter-modifier-client',
  templateUrl: './ajouter-modifier-client.component.html',
  styleUrls: ['./ajouter-modifier-client.component.css']
})
export class AjouterModifierClientComponent implements OnInit {
private client:Client;
  constructor(private router:Router,private clientService:ClientService) { }

  ngOnInit() {
    this.client=this.clientService.getClient();
  }
  processForm(){
    if(this.client.code==undefined){
      this.clientService.AjouterClient(this.client).subscribe((client)=>{
        alert('L\'ajout a été bien effectué');
        this.router.navigateByUrl("/clients")
        },(error)=>{
          alert('erreur d\'ajout  ');
        }
      );

    } else{ 
    this.clientService.ModifierClient(this.client).subscribe((client)=>{
      //console.log(Client);
      alert('La modification a été bien effectuée');
      this.router.navigateByUrl("/clients");
      },(error)=>{
        alert('erreur de modification  ');
      }
    );
    
  }
    }
      
  }



