import { Component, OnInit } from '@angular/core';
import { Router } from '../../../../node_modules/@angular/router';
import { TraitementService } from '../../_services/traitement.service';
import { User } from '../../classes/user';

@Component({
  selector: 'app-ajouter-modifier-utilisateur',
  templateUrl: './ajouter-modifier-utilisateur.component.html',
  styleUrls: ['./ajouter-modifier-utilisateur.component.css']
})
export class AjouterModifierUtilisateurComponent implements OnInit {
user:User;
  constructor(private router:Router,private traitService:TraitementService) { }

  ngOnInit() {
  }
 
  processForm(){
    if(this.user.num==undefined){
    this.traitService.AjouterUser(this.user).subscribe((donnees)=>{
    console.log(donnees);
    alert('L\'ajout a été bien effectué');
    this.router.navigateByUrl("/utilisateurs")
    },(error)=>{
      alert('erreur d\'ajout  ');
    }
  );
  
}

}
  }




