import { Component, OnInit } from '@angular/core';
import { GrossisteServiceService } from '../../_services/grossiste-service.service';
import { Grossiste } from '../../classes/grossiste';
import { Router } from '@angular/router';

@Component({
  selector: 'app-list-grossistes',
  templateUrl: './list-grossistes.component.html',
  styleUrls: ['./list-grossistes.component.css']
})
export class ListGrossistesComponent implements OnInit {
 listgrossistes;
 
 
  constructor(private router:Router ,private grossisteService:GrossisteServiceService ) {
    this.grossisteService.AfficherGrossistes().subscribe(donnees=>{this.listgrossistes=donnees
  })
}
  ngOnInit() {
    
  }
  AjouterGrossiste(){
let grossiste = new Grossiste();
this.grossisteService.setterGrossiste(grossiste);
this.router.navigateByUrl("/ajouterModifierGrossiste");

  }
  AfficherGrossiste(grossiste){
    this.grossisteService.setterGrossiste(grossiste);
    this.router.navigateByUrl("/ajouterModifierGrossiste")
  }
}



