import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CommandeService } from '../../_services/commande.service';
import { Commande } from '../../classes/commande';
import { LigneCommande } from '../../classes/ligne-commande';
import { LigneCommandeService } from '../../_services/ligne-commande.service';

@Component({
  selector: 'app-list-command',
  templateUrl: './list-command.component.html',
  styleUrls: ['./list-command.component.css']
})
export class ListCommandComponent implements OnInit {

  constructor(private router:Router, private commandeService:CommandeService,private ligneCommandeService:LigneCommandeService) { }

  ngOnInit() {
  }
  AjouterCommande(){
  let commande= new Commande();
  this.commandeService.setterCommande(commande);
  //let ligneCommande= new LigneCommande();
  //this.ligneCommandeService.setterLigneCommande(ligneCommande)
  this.router.navigateByUrl("/ajouterModifierCommande")
  }
}



