import { Component, AfterViewInit } from '@angular/core';
import { Router } from '@angular/router';
import {JwtHelper} from 'angular2-jwt';
import { TraitementService } from '../../_services/traitement.service';
import { User } from '../../classes/user';


@Component({
  selector: '[app-header]',
  templateUrl: './app-header.component.html',
})
export class AppHeader {
  private date="21/10/2018";
  private jwtoken=null;
  private roles:Array<any>;
  private userConnecter=null;
  private username;
  private user;
constructor(private router:Router, private traitService:TraitementService) {
  this.jwtoken=localStorage.getItem('token');
  let jwtHelper=new JwtHelper();
  this.username=jwtHelper.decodeToken(this.jwtoken).sub;
  this.roles=jwtHelper.decodeToken(this.jwtoken).roles;
   for(let r of this.roles){
   this.userConnecter=r.authority;
   } 
 } 
  ngOnInit() { 
    this.traitService.RechercherUser(this.username).subscribe(resp=>{
      this.user=resp;
      console.log(this.user);
     })    
  }
  Logout(){
    localStorage.removeItem('token');
    this.router.navigateByUrl('/login');
  }
  ngAfterViewInit()  {
	}
}



