import { Component, OnInit } from '@angular/core';
import { Router } from '../../../../node_modules/@angular/router';
import { TraitementService } from '../../_services/traitement.service';

@Component({
  selector: 'app-utilisateurs',
  templateUrl: './utilisateurs.component.html',
  styleUrls: ['./utilisateurs.component.css']
})
export class UtilisateursComponent implements OnInit {
private users;
  constructor(private router:Router, private traitService:TraitementService) {
   }
  
  ngOnInit() {

    this.traitService.getUsers().subscribe(user=>{this.users=user}),
    console.log(this.users)
    error=>{

    }
    
  }

  AjouterUser(){
 this.router.navigateByUrl("/ajouterModifierUtilisateur");
  }
}



