import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Article } from '../classes/article';
@Injectable()
export class ArticleService {
  private host:string="http://localhost:8080";
  private jwtoken=null;
  private article:Article;
  constructor(private http:HttpClient) { }

  saveToken(jwt:string){
    this.jwtoken=jwt;
    localStorage.setItem('token',jwt);
}
loadtoken(){
  this.jwtoken=localStorage.getItem('token');
  }
  getArticles(){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.get(this.host+"/listArticles",{headers:new HttpHeaders({'Authorization':this.jwtoken})})
  }
  AjouterArticle(article:Article){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.post(this.host+"/ajouterArticle",article,{headers:new HttpHeaders({'Authorization':this.jwtoken})});
  }
  ModifierArticle(article:Article){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.put(this.host+"/modifierArticle",article,{headers:new HttpHeaders({'Authorization':this.jwtoken})});
  }
  getterArticle(){
    return this.article;
  }
  setterArticle(article){
    this.article=article;
  }
}



