import { Component, OnInit } from '@angular/core';
import { GrossisteServiceService } from '../../_services/grossiste-service.service';
import { Grossiste } from '../../classes/grossiste';
import { Router } from '@angular/router';

@Component({
  selector: 'app-ajouter-modifier-grossiste',
  templateUrl: './ajouter-modifier-grossiste.component.html',
  styleUrls: ['./ajouter-modifier-grossiste.component.css']
})
export class AjouterModifierGrossisteComponent implements OnInit {
private grossiste:Grossiste;
  constructor(private router:Router,private grossisteService:GrossisteServiceService) {
    this.grossiste=this.grossisteService.getterGrossite();
   }

  ngOnInit() {
  }
  processForm(){
    if(this.grossiste.num==undefined){
      this.grossisteService.AjouterGrossiste(this.grossiste).subscribe((client)=>{
        alert('L\'ajout a été bien effectué');
        this.router.navigateByUrl("/grossistes")
        },(error)=>{
          alert('erreur d\'ajout  ');
        }
      );

    } else{ 
    this.grossisteService.ModifierGrossiste(this.grossiste).subscribe((client)=>{
      //console.log(Client);
      alert('La modification a été bien effectuée');
      this.router.navigateByUrl("/grossistes");
      },(error)=>{
        alert('erreur de modification  ');
      }
    );
    
  }
    }
}



