import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Grossiste } from '../classes/grossiste';

@Injectable()
export class GrossisteServiceService {
 private host:string="http://localhost:8080";
 private jwtoken=null;
 private grossiste:Grossiste;
  constructor(private http:HttpClient) { }

  saveToken(jwt:string){
    this.jwtoken=jwt;
    localStorage.setItem('token',jwt);
}
loadtoken(){
  this.jwtoken=localStorage.getItem('token');
  }
  
  AfficherGrossistes(){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.get(this.host+"/listgrossistes",{headers:new HttpHeaders({'Authorization':this.jwtoken})})
  
  }
  AjouterGrossiste(grossiste:Grossiste){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.post(this.host+"/ajouterGrossiste",grossiste,{headers:new HttpHeaders({'Authorization':this.jwtoken})});
  }
  ModifierGrossiste(grossiste:Grossiste){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.put(this.host+"/modifierGrossiste",grossiste,{headers:new HttpHeaders({'Authorization':this.jwtoken})});
  }
  getterGrossite(){
    return this.grossiste;
  }
  setterGrossiste(grossiste){
    this.grossiste=grossiste;
  }
}



