import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Client } from '../classes/client';
import { identifierName } from '@angular/compiler';

@Injectable()
export class ClientService {
private host:string="http://localhost:8080";
private jwtoken=null;
private client:Client;
private clients;
  constructor(private http:HttpClient) { }

  saveToken(jwt:string){
    this.jwtoken=jwt;
    localStorage.setItem('token',jwt);
}
 loadtoken(){
    this.jwtoken=localStorage.getItem('token');
  }

 AfficherClients(){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.get(this.host+"/listclients",{headers:new HttpHeaders({'Authorization':this.jwtoken})})
  
  }

  AjouterClient(client:Client){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.post(this.host+"/ajouterClient",client,{headers:new HttpHeaders({'Authorization':this.jwtoken})});
  }
  ModifierClient(client:Client){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.put(this.host+"/modifierClient",client,{headers:new HttpHeaders({'Authorization':this.jwtoken})});
  }
  SupprimerClient(codeClient){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.delete(this.host+"/supprimerClient/"+codeClient,{headers:new HttpHeaders({'Authorization':this.jwtoken})});
    
  }
  RechercherClientparCodeClient(codeClient){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.get(this.host+"/findClientByCin/"+codeClient,{headers:new HttpHeaders({'Authorization':this.jwtoken})})
  }

  RechercherClientsparNom(nomClient){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.get(this.host+"/findClientsByNom/"+nomClient,{headers:new HttpHeaders({'Authorization':this.jwtoken})})
  }
  RechercherClientNumpiece(numPiece){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.get(this.host+"/findClientByNumpiece/"+numPiece,{headers:new HttpHeaders({'Authorization':this.jwtoken})})
  }
  /*
  getClientsGraphe(){
    if(this.jwtoken==null) this.loadtoken();
    return this.http.get(this.host+"/listClients",{headers:new HttpHeaders({'Authorization':this.jwtoken})})
  
  }
  */
  setClient(client){
    this.client=client;
  }
  getClient(){
    return this.client;
  }
  setClients(clients){
    this.clients=clients;
  }
  getClients2(){
    return this.clients;
  }
}



