import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GrossisteServiceService } from '../../_services/grossiste-service.service';
import { Commande } from '../../classes/commande';
import { CommandeService } from '../../_services/commande.service';
import { ArticleService } from '../../_services/article.service';
import { LigneCommande } from '../../classes/ligne-commande';
import { LigneCommandeService } from '../../_services/ligne-commande.service';
import { Article } from '../../classes/article';
import { jsonpCallbackContext } from '@angular/common/http/src/module';

@Component({
  selector: 'app-ajouter-modifier-commande',
  templateUrl: './ajouter-modifier-commande.component.html',
  styleUrls: ['./ajouter-modifier-commande.component.css']
})
export class AjouterModifierCommandeComponent implements OnInit {
  private commande= new Commande();
  private listgrossistes;
  private listArticles;
  private article= new Article();
  ligneCommande= new LigneCommande();

  constructor(private router:Router ,private grossisteService:GrossisteServiceService, private commandeService:CommandeService, private articleService:ArticleService, private ligneCommandeService:LigneCommandeService ) {
    this.articleService.getArticles().subscribe(donnees=>{this.listArticles=donnees
    })
}
  ngOnInit() {  
  }
  
 
  processForm(){
    if(this.commande.num==undefined){
     this.ligneCommande.commande=this.commande;
      this.commande.ligne_Commandes= new Array(); 
      let  lignecommandeJson= JSON.parse(JSON.stringify(this.ligneCommande))
      this.commande.ligne_Commandes.push(lignecommandeJson);
      this.commandeService.AjouterCommande(this.commande).subscribe((commande)=>{
        console.log(this.commande.ligne_Commandes.length);
        alert('L\'ajout a été bien effectué');
        this.router.navigateByUrl("/listCommandes")
        },(error)=>{
          alert('erreur d\'ajout  ');
        }
      );

    } else{ 
    this.commandeService.ModifierCommande(this.commande).subscribe((commande)=>{
      //console.log(Client);
      alert('La modification a été bien effectuée');
      this.router.navigateByUrl("/listCommandes");
      },(error)=>{
        alert('erreur de modification  ');
      }
    );
    
  }
    }
}



