import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LayoutComponent } from './/layouts/layout.component';
import { HomeComponent } from './pages/home/home.component';
import { ColorsComponent } from './pages/ui/colors/colors.component';
import { TypographyComponent } from './pages/ui/typography/typography.component';
import { PanelsComponent } from './pages/ui/panels/panels.component';
import { TabsComponent } from './pages/ui/tabs/tabs.component';
import { AlertsComponent } from './pages/ui/alerts/alerts.component';
import { CardsComponent } from './pages/ui/cards/cards.component';
import { BadgesProgressComponent } from './pages/ui/badges-progress/badges-progress.component';
import { ListComponent } from './pages/ui/list/list.component';
import { IconsComponent } from './pages/ui/icons/icons.component';
import { ButtonsComponent } from './pages/ui/buttons/buttons.component';

import { FormBasicComponent } from './pages/forms/form-basic/form-basic.component';
import { InputMasksComponent } from './pages/forms/input-masks/input-masks.component';
import { FormValidationComponent } from './pages/forms/form-validation/form-validation.component';
import { TextEditorsComponent } from './pages/forms/text-editors/text-editors.component';
import { FormAdvancedComponent } from './pages/forms/form-advanced/form-advanced.component';

import { ChartjsComponent } from './pages/charts/chartjs/chartjs.component';
import { MorrisChartComponent } from './pages/charts/morris-chart/morris-chart.component';
import { SparklineChartComponent } from './pages/charts/sparkline-chart/sparkline-chart.component';

import { LoginComponent } from './pages/login/login.component';
import { Error404Component } from './pages/error-404/error-404.component';
import { Error500Component } from './pages/error-500/error-500.component';
//les importations que je vais l'ajouter moi meme
import {FormsModule} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {CommonModule} from '@angular/common';
import { UtilisateursComponent } from './pages/utilisateurs/utilisateurs.component';
import { AjouterModifierUtilisateurComponent } from './pages/ajouter-modifier-utilisateur/ajouter-modifier-utilisateur.component';
import { ListClientsComponent } from './pages/list-clients/list-clients.component';
import { AjouterModifierClientComponent } from './pages/ajouter-modifier-client/ajouter-modifier-client.component';
import { AjouterModifierArticleComponent } from './pages/ajouter-modifier-article/ajouter-modifier-article.component';
import { ListGrossistesComponent} from './pages/list-grossistes/list-grossistes.component';
import { ListArticlesComponent } from './pages/list-articles/list-articles.component';
import { AjouterModifierCommandeComponent } from './pages/ajouter-modifier-commande/ajouter-modifier-commande.component';
import { ListCommandComponent } from './pages/list-command/list-command.component';
import { InventaireComponent } from './pages/inventaire/inventaire.component';
import { BonLivraisonComponent } from './pages/bon-livraison/bon-livraison.component';
import { BonReceptionComponent } from './pages/bon-reception/bon-reception.component';
import { AvoirComponent } from './pages/avoir/avoir.component';
import { ListVentesComponent } from './pages/list-ventes/list-ventes.component';
import { AjouterModifierVenteComponent } from './pages/ajouter-modifier-vente/ajouter-modifier-vente.component';
import { AjouterModifierGrossisteComponent } from './pages/ajouter-modifier-grossiste/ajouter-modifier-grossiste.component';

const routes: Routes = [
    {path: '', redirectTo: 'login', pathMatch: 'full'},

    {
        "path": "",
        "component": LayoutComponent,
        "children": [
            {
                path: "index",
                component: HomeComponent
            },
            {
                path: "utilisateurs",
                component: UtilisateursComponent
            },
            {
                path: "ajouterModifierUtilisateur",
                component: AjouterModifierUtilisateurComponent
            },
            {
                path: "clients",
                component: ListClientsComponent
            },
            {
                path: "ajouterModifierClient",
                component: AjouterModifierClientComponent
            },
            {
                path: "ajouterModifierArticle",
                component: AjouterModifierArticleComponent
            },

            {
                path: "grossistes",
                component: ListGrossistesComponent
            },
            {
            path: "articles",
            component: ListArticlesComponent
        },
        {
            path: "ajouterModifierCommande",
            component: AjouterModifierCommandeComponent
        },
        {
            path: "listCommandes",
            component: ListCommandComponent
        },
        {
            path: "ajouterModifierCommande",
            component: AjouterModifierCommandeComponent
        },
        {
            path: "ajouterModifierArticle",
            component: AjouterModifierArticleComponent
        },
        {
            path: "inventaire",
            component: InventaireComponent
        },
        {
            path: "bonlivraison",
            component: BonLivraisonComponent
        },
        {
            path: "bonreception",
            component: BonReceptionComponent
        },
        {
            path: "avoir",
            component: AvoirComponent
        },
        {
            path: "ventes",
            component: ListVentesComponent
        },
        {
            path: "ajouterModifierVente",
            component: AjouterModifierVenteComponent
        },
        {
            path: "ajouterModifierGrossiste",
            component: AjouterModifierGrossisteComponent
        },
        
        
            {
                path: "ui/colors",
                component: ColorsComponent
            },
            {
                path: "ui/typography",
                component: TypographyComponent
            },
            {
                path: "ui/panels",
                component: PanelsComponent
            },
            {
                path: "ui/buttons",
                component: ButtonsComponent
            },
            {
                path: "ui/tabs",
                component: TabsComponent
            },
            {
                path: "ui/alerts",
                component: AlertsComponent
            },
            {
                path: "ui/badges_progress",
                component: BadgesProgressComponent
            },
            {
                path: "ui/lists",
                component: ListComponent
            },
            {
                path: "ui/cards",
                component: CardsComponent
            },
            {
                path: "ui/icons",
                component: IconsComponent
            },
            {
                path: "forms/form_basic",
                component: FormBasicComponent
            },
            {
                path: "forms/form_advanced",
                component: FormAdvancedComponent
            },
            {
                path: "forms/form_masks",
                component: InputMasksComponent
            },
            {
                path: "forms/form_validation",
                component: FormValidationComponent
            },
            {
                path: "forms/text_editors",
                component: TextEditorsComponent
            },
            {
                path: "charts/chartjs",
                component: ChartjsComponent
            },
            {
                path: "charts/charts_morris",
                component: MorrisChartComponent
            },
            {
                path: "charts/charts_sparkline",
                component: SparklineChartComponent
            }

        ]
    },
    {
        "path": "login",
        "component": LoginComponent
    },

    {
        "path": "error_404",
        "component": Error404Component
    },
    {
        "path": "error_500",
        "component": Error500Component
    },
    {
        "path": "**",
        "redirectTo": "error_404",
        "pathMatch": "full"
    },
];

@NgModule({
  declarations: [
    HomeComponent,
    ColorsComponent,
    TypographyComponent,
    PanelsComponent,
    TabsComponent,
    AlertsComponent,
    CardsComponent,
    BadgesProgressComponent,
    ListComponent,
    IconsComponent,
    ButtonsComponent,
    FormBasicComponent,
    InputMasksComponent,
    FormValidationComponent,
    TextEditorsComponent,
    FormAdvancedComponent,
    ChartjsComponent,
    MorrisChartComponent,
    SparklineChartComponent,
    LoginComponent,
    Error404Component,
    Error500Component,
    
  ],
  imports: [ RouterModule.forRoot(routes),FormsModule,HttpClientModule,CommonModule],
  exports: [
    RouterModule,FormsModule,HttpClientModule,CommonModule
  ]
})

export class AppRoutingModule { }



